.Search-Menu {
    width: 100%;
    height: 40%;

    position: absolute;
    bottom: 0; left: 0;

    background-color: white;

    border-top: 4px solid;
    border-color: var(--blue);

    border-collapse: collapse;
}

.Scroll::-webkit-scrollbar {
    display: none;
}

.Scroll {
    -ms-overflow-style: none; /* 인터넷 익스플로러 */
    scrollbar-width: none; /* 파이어폭스 */
} 

.MenuElement {
    width: 100%;
    top: 0;
    padding-top: 0.3rem;
    padding-bottom: 0.5rem;

    border-bottom: 2px solid;
    border-color: rgb(107 114 128);
}

.MenuItem-Container {
    position: absolute;
    width: 100%;
    text-align: center;
    overflow: scroll;
    padding: 0px;
}

.MenuItem-Position {
    height: 80%;    
}

.MenuHeader-Container {
    width: 100%;
    background-color: white;
}

.MenuHeader-Position {
    height: 20%;
}

.MenuItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    font-size: 0.8rem;

    background-color: white;
}

.MenuItem:hover{
    background-color: rgb(203 213 225);
}

.MenuHeader {
    display: flex;
    flex-direction: row;
    text-align: center;
    font-size: 0.8rem;
    
    border-bottom: 4px solid;
}

.MenuFilter {
    width: 100%;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.MenuBtn-Container {
    display: flex;
    flex-direction: row;

    align-items: center;
}

.MenuBtn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;

    font-weight: 500;

    border-radius: 9999px;

    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    
    padding-left: 1rem;
    padding-right: 1rem;

    margin-left: 1.5rem;
    margin-right: 1.5rem;

    overflow: hidden;
    text-overflow: ellipsis;

    color: white;
}

.FilterBtn {
    background-color: var(--blue);
}

.FilterBtn:hover {
    background-color: var(--lightblue);
}

.CloseBtn {
    background-color: var(--orange);
}

.CloseBtn:hover {
    background-color: var(--yellow);
}

.FilterClearBtn {
    margin-left: 0.25rem;
    color: rgb(107 114 128);
    width: 1rem;
    height: 1rem;
    line-height: 1rem;
    font-weight: 600;
    border-radius: 9999px;
    background-color: white;
}

.FilterClearBtn:hover {
    color: rgb(75 85 99);
}

.DeptCD { width: 17%; white-space: pre-line; }
.CategoryCD { width: 10%; }
.Grade { width: 5%; }
.Subject { width: 18%; white-space: pre-line; }
.Division { width: 6%; }
.Credit { width: 4%; }
.Professor { width: 7%; }
.Time { width: 20%; white-space: pre-line; }
.Remark { width: 25%; }

